.navbar {
    height: 50px;
    border: 1px solid #93d7b2;
    background-color: #a1ddbc;
    display: flex;
    padding-left: 10px;
    
}

.navItem {
    color: white;
    display: inline-block;
    padding-right: 30px;
    padding-left: 30px;
    text-decoration: none;
    padding-top: 10px;
}
.navItem header {
    font-size: large;
    font-weight: 900;
    font-family: "Comic Sans MS", "Comic Sans", cursive;
}

.navButton:hover {
    background-color: #379662;
}
