.table-container {
  /* justify-content: center; */
  margin-top: 50px;
}

table {
  border-collapse: collapse;
  margin-top: 20px;
  margin-bottom: 100px;
  margin-right: 80px;
  margin-left: 80px;
  font-size: 14px;
  font-family: sans-serif;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  width: 90%;
  min-width: 500px;
}

table thead tr {
  background-color: #4bbd7f;
  color: #ffffff;
  text-align: left;
}

table th,
table td {
  padding: 10px 15px;
}

table tbody tr {
  border-bottom: 1px solid #dddddd;
}

table tbody tr:nth-of-type(even) {
  background-color: #f6f6f6;
}

table tbody tr:last-of-type {
  border-bottom: 2px solid #4bbd7f;
}

table tbody tr.active-row {
  font-weight: bold;
  color: #4bbd7f;
}

/* table td:first-child input {
  width: 50px;
} */

table td:nth-child(2) input {
  width: 120px;
}

table button {
  font-size: 20px;
  border-radius: 60px;
  height: 40px;
  width: 40px;
  border: 1px solid #ccc;
  color: #ffb918;
}
table button:hover {
  background-color: #c2c2c2;
}

table button[name="cancel"] {
  color: #7b7b7b;
}

table button[name="done"] {
  color: #4bbd7f;
}

table input,
select {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 2px;
}

table .edit-cell {
  gap: 5px;
  text-align: right;
  margin-right: 20px;
}
table tr td:first-child { display: none; }
table tr th:first-child { display: none; }
